import {
  ChangeEvent,
  ClassAttributes,
  ComponentProps,
  FC,
  useMemo,
} from 'react';
import classNames from 'classnames';

// Styles
import './Toggle.scss';

// Types
import { TestMetadata } from 'types';

export type ToggleLabelStyle = 'onOff' | 'yesNo' | 'checkOff' | 'checkNo';

export interface ToggleProps {
  /* name for toggle*/
  switchName?: string;
  /* ID for toggle */
  switchId?: string;
  /* Custom class name(s) for component container */
  className?: string;
  /** Label style for the toggle */
  labelStyle?: ToggleLabelStyle;
  /* Disables the toggle */
  disabled?: boolean;
  /** method to control changing the toggle */
  onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
  /** value to set the toggle */
  checked?: boolean;
  /** ref form inner input **/
  inputRef?: ClassAttributes<HTMLInputElement>['ref'];
  /** Additional input props for Input, based on the inputComponent */
  inputProps?: Partial<ComponentProps<'input'>>;
  disabledShake?: boolean;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const Toggle: FC<ToggleProps> = ({
  className,
  labelStyle,
  disabled,
  onChange,
  checked,
  switchName,
  switchId,
  inputRef,
  inputProps,
  testMetadata,
  disabledShake = true,
  ...otherProps
}) => {
  const toggleClassNames = useMemo(
    () =>
      classNames(
        'sb-toggle',
        {
          'sb-toggle_on-label': labelStyle === 'onOff',
          'sb-toggle_off-label':
            labelStyle === 'onOff' || labelStyle === 'checkOff',
          'sb-toggle_yes-label': labelStyle === 'yesNo',
          'sb-toggle_no-label':
            labelStyle === 'yesNo' || labelStyle === 'checkNo',
          'sb-toggle_check-label':
            labelStyle === 'checkOff' || labelStyle === 'checkNo',
          'sb-toggle_disabled': disabled,
        },
        className,
      ),
    [className, disabled, labelStyle],
  );

  const sliderClassNames = useMemo(
    () =>
      classNames('sb-toggle__switch__slider', {
        'sb-toggle__switch__slider-shake': disabledShake,
      }),
    [disabledShake],
  );

  return (
    <div {...otherProps} className={toggleClassNames} role="switch">
      <label className="sb-toggle__switch">
        <input
          name={switchName}
          type="checkbox"
          id={switchId}
          disabled={disabled}
          checked={checked}
          onChange={onChange}
          ref={inputRef}
          role="checkbox"
          aria-checked={checked}
          {...inputProps}
          {...testMetadata}
        />
        <span className={sliderClassNames} />
      </label>
    </div>
  );
};

export default Toggle;
